import Router from 'koa-router'
import config from '../config'
import db from '../models'
import checkToken from '../middlewares/check-token'

const router = new Router({
  prefix: config.app.routerBaseApi
})

const user = require('../controllers/user')
const tag = require('../controllers/tag')
const article = require('../controllers/article')

router
  .get('/user', user.getUserInfo)
  .patch('/user', user.patchUserInfo)
  .post('/login', user.login)

router
  .get('/tags/:id?', tag.getTagsOrArticles)
  .post('/tag', tag.postTag)
  .patch('/tag', tag.patchTag)
  .del('/tag/:id?', tag.deleteTag)

router
  .get('/article/:id?', article.getArticle)
  .get('/articles/:page?/:limit?', article.getArticles)
  .get('/private-articles', article.getPrivateArticles)
  .get('/search/:keyword?', article.search)
  .get('/archives', article.archives)
  .post('/article', article.postArticle)
  .post('/upload', article.upload)
  .patch('/article', article.patchArticle)
  .del('/article/:id?', article.deleteArticle)

export default router


